.presentation {
  @include marginLeft($m-hor, 0, 0);
  @include marginRight($m-hor, 0, 0);

  &__photo {
    @include marginLeft(0, $m-hor, 0);
    @include marginRight(0, $m-hor, 0);

    transition: transform 0.3s;

    &:hover {
      cursor: pointer;
      transform: scale(1.05);
    }
  }

  &__img {
    display: block;
    width: 100%;
    max-width: 846px;

    margin: 0 auto;
  }
}
